@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body{
  font-family: Roboto;

}

.App{
  display: flex;
  flex-direction: column;
  background-color: #E5E5E5;
}
.Input {
}

.Input label {
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color:  #424242;
}

.Input input {
    display: block;
    box-sizing: border-box;
    border: 1px solid #C9CACC;
    border-radius: 5px;
    padding: 7px;
    margin: 0 0 5px;
    width: 100%;
    outline: none;
    font-weight: 300;
    font-size: 16px;
    line-height: 18px;
    transition: all 300ms ease-in-out;
}

.Input span {
    color: #f01f30;
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
}

.Input.invalid label {
    color: #f01f30;
}
.Input.invalid input {
    border: 1px solid red;
    color: red;
}
.fullscreen{
  min-height: 100vh;
  min-width: 100%;
  background-image: url(/testHotel/static/media/background.53a28ade.png);
  background-size: 100% 100%;
  background-position: top left;
  background-repeat: no-repeat;
  object-fit: cover;
}

.form{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form form{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 16px;
  box-sizing: border-box;
  flex: 0 0 382px;
  background-color: #fff;
  padding: 32px;
}

.form h1{
  margin: 0 0 32px 0;
  align-self: center;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #424242;
}
form .Input{
  margin-bottom: 24px;
}
.Button{
    width: 100%;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    background: linear-gradient(104.34deg, #41522E -15.34%, #BE8022 145.95%);
    color: #fff;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    border: none;
}

.Button:active, .Button:hover{
    cursor: pointer;
}
.Search{
    grid-area: search;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 16px;
    box-sizing: border-box;
    background-color: #fff;
    padding: 32px;
}
.Hotels{
    display: grid;
    max-height: calc(100% - 100px);
    grid-template-areas: 'search hotels hotels' 'fovarite hotels hotels';
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 410px 462px;
    grid-gap: 24px;
    gap: 24px;
    margin: 0 196px 30px 196px;

}
.Favorites{
    grid-area: fovarite;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: 16px;
    box-sizing: border-box;
    background-color: #fff;
    padding: 32px;

}
.favorites__control{
    margin-top: 32px;
    margin-bottom: 32px;
}
.favorites__header{
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;

}
.Order{
    font-size: 14px;
    line-height: 20px;
    color: #99A0A3;
    border: 1px solid #99A0A3;
    border-radius: 4px;
    padding: 4px 20px 4px 4px;
    cursor: pointer;
    position: relative;
}
.Order:not(:last-child){
    margin-right: 16px;
}
.Order.desc, .Order.ask{
    border-color:#41522E;
    color: #41522E;
}
.Order.desc:after{
    border-color: #41522E;
}
.Order.ask:before{
    border-color:  #41522E;
}
.Order:before,.Order:after{
  content: "";
  position: absolute;
  border: solid #99A0A3;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 2px;
  right: 5px;
}
.Order:before{
  transform: rotate(-135deg);
  top: 8px;
}
.Order:after{
    transform: rotate(45deg);
    bottom: 8px;
}

.Card{
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid rgba(135, 135, 135, 0.2);
    padding-bottom: 16px;
}
.Card:not(:last-child){
    margin-bottom: 16px;
}
.card__content{
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.card__hotel{
    font-weight: 300;
    font-size: 17px;
    line-height: 22px;
    color: #424242;
}

@media (max-width: 350px) {
    .card__hotel{
        font-size: 10px;
    }
}
.card__date{
    font-size: 14px;
    line-height: 22px;
    color: #878787;
}
.card__top{
    display: flex;
    justify-content: space-between;
}
.card__favor{
    display: flex;
    justify-content: flex-end;
    margin-right: 10px;
}

.card__favor svg{
    width: 21px;
    height: 18px;
}

.card__bottom{
    display: flex;
    justify-content: space-between;
}
.card__center{
    display: flex;
    justify-content: flex-start;
}
.card__price{
    font-weight: 300;
    font-size: 11px;
    line-height: 22px;
    color: #878787;
}
.card__price span{
    font-size: 17px;
    line-height: 22px;
    color: #424242;
    padding-left: 20px;
    font-weight: 400;
}

.card__photo{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:rgba(65, 82, 46, 0.05);
    border-radius: 50%;
    width: 64px;
    height: 64px;
    margin-right: 24px;
}


.Cards{
    width: 100%;
     min-height: -webkit-min-content;
     min-height: -moz-min-content;
     min-height: min-content;
    padding-right: 10px;
}
.cards__wrapper{
    flex: 1 1;
    display: flex;
    overflow: auto;
}
html * /* override x.xhtml.ru style */
{
  scrollbar-width: thin;
  scrollbar-color: #424242;
}

*::-webkit-scrollbar,
html *::-webkit-scrollbar {
  height: 100px;
  width: 2px;
}
*::-webkit-scrollbar-track,
html *::-webkit-scrollbar-track {
  background: #E7E7E7;;
}
*::-webkit-scrollbar-thumb,
html *::-webkit-scrollbar-thumb {
  background-color: #424242;
  border-radius: 5px;
  border: 3px solid #424242;
}
.Result{
    grid-area: hotels;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: 16px;
    box-sizing: border-box;
    background-color: #fff;
    padding: 32px;
}
.results__header{
    display: flex;
    justify-content: space-between;
    margin-bottom: 28px;
}
.results__date{
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #41522E;
}
.results__info{
    font-style: normal;
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    display: flex;
    align-items: flex-start;
    color: #424242;
}
.results__info span{
    position: relative;
}
.results__info span:not(:last-child):after{
  content: "";
  position: absolute;
  right: 10px;
    top: 15px;
  border: solid #99A0A3;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 5px;
  transform: rotate(-45deg);
}
.results__info span:not(:last-child){
    padding-right: 30px;
}

.swiper__wrap{
    margin-bottom: 28px;
}
.favorite__wrap{
    margin: 0 0 28px 0;
}
.favorite__wrap p{
    margin: 0;
    font-weight: 300;
    font-size: 17px;
    line-height: 22px;
    color: #424242;
}
.favorite__wrap p span{
    font-weight: 500;
    color: #41522E;
}
.Slider{
    max-width: 100%;
}

.Header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 31px;
    min-height: 92px;
}
.logout{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 20px;
    color: #41522E;
    cursor: pointer;
}
.logout p{
    margin-right: 15px;
}
.header__title{
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
}
