.Header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 31px;
    min-height: 92px;
}
.logout{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 20px;
    color: #41522E;
    cursor: pointer;
}
.logout p{
    margin-right: 15px;
}
.header__title{
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
}